.app-header {
  height: 100%;
  background-color: rgb(32, 7, 19);
  content: "";
  display: block;
  width: 100%;
  z-index: 1;
}

.app-header img {
  width: 100%;
  height: 600px;
  object-fit: cover;
}

.app-header h1 {
  font-size: 3.6em;
  margin: 30px 0 0;
  font-family: "Montserrat", "Helvetica Neue", Arial, sans-serif;
  font-weight: 300;
  text-align: center;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}
.app-header h3 {
  margin: 90px 0 0;
  font-size: 21.9px;
  font-family: "Montserrat", "Helvetica Neue", Arial, sans-serif;
  font-weight: 500;
  text-align: center;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}
